.profile{
  color: #fff;
  padding: 40px 0 50px;
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 930px){
  .profile{
    flex-direction: column;
    align-items: center;
  }
}
@media screen and (max-width: 540px){
  .profile{
    padding: 42px 0 36px;
  }
}